import { React } from "src/utils/view"

import { PostFrontmatter } from "src/utils/types"

type Props = {
  frontmatter: PostFrontmatter
  html: string
}

export default function FullPost({ frontmatter, html }: Props) {
  return (
    <>
      <h1>{frontmatter.title}</h1>
      <div>{frontmatter.date}</div>
      <div dangerouslySetInnerHTML={{ __html: html }} />
      <div>{frontmatter.author}</div>
    </>
  )
}
